<template>
    <div class="box">
        <span v-if="!hasProduct()">No products :/</span>
        <div v-for="(product, index) in getProductsInCart" :key="index" class="box-item">
            <h3 class="item-name">{{ product.name }}</h3>
            <span class="item-amount">Amount: {{ product.quantity }}</span>
            <span class="item-price">‎€ {{ product.price * product.quantity }}</span>
        </div>
        <div class="cart-info" v-if="hasProduct()">
            <span>Total: €{{ getTotalProductsInCart }}</span>
            <router-link to="/checkout">
                <btn btnColor="btn btn-small btn-info"
                     @click.native="showPopupCart()">
                    View cart
                </btn>
            </router-link>
        </div>
    </div>
</template>

<script>
    import {mapGetters, mapActions} from 'vuex';
    import btn from './Btn';

    export default {
        components: {
            btn,
        },
        methods: {
            ...mapActions([
                'showOrHiddenPopupCart',
            ]),
            hasProduct() {
                return this.getProductsInCart.length > 0;
            },
            totalPrice() {
                return this.getProductsInCart.reduce((current, next) =>
                    current + next.price, 0);
            },
            showPopupCart() {
                this.showOrHiddenPopupCart();
            },
        },
        computed: {
            ...mapGetters([
                'getProductsInCart',
                'getTotalProductsInCart',
            ]),
        },
    };
</script>

